<template>
  <div id="add-blog">
    <nav-bar>
      <span>my-blog</span>
    </nav-bar>

    <h3>编辑</h3>
    <form v-if="!submited">
      <label>留言标题</label>
      <input type="text" v-model="blog.title" required>
    
      <label>留言内容</label>
      <textarea v-model="blog.content"></textarea>
    
      <div id="checkboxes">
        <!-- <div class="checkCenter"> -->
          <label>Vue.js</label>
          <input type="checkbox" value="Vue.js" v-model="blog.categories">
          <label>Node.js</label>
          <input type="checkbox" value="Node.js" v-model="blog.categories">
          <label>React.js</label>
          <input type="checkbox" value="React.js" v-model="blog.categories">
          <label>Angular4</label>
          <input type="checkbox" value="Angular4" v-model="blog.categories">
        <!-- </div> -->
      </div>

      <label>作者：</label>
      <select v-model="blog.author">
        <option v-for="(author, index) in authors" :key="index">
          {{author}}
        </option>
      </select>
    </form>

    <div v-if="submited">
      <h3>留言发布成功</h3>
    </div>

    <div id="preview">
      <h3>总览</h3>
      <p>标题：{{blog.title}}</p>
      <p>内容：</p>
      <p>{{blog.content}}</p>
      <p>分类：</p>
      <ul>
        <li v-for="(category, index) in blog.categories" :key="index">
          {{category}}
        </li>
      </ul>
      <p>作者：{{blog.author}}</p>

      <button @click.prevent="putdata">编辑留言</button>
    </div>
    <div class="pngImg">
      <img src='../assets/猫咪.png'>
    </div>
  </div>
</template>
<script>
// https://jsonplaceholder.typicode.com/
// https://jsonplaceholder.typicode.com/posts
import NavBar from './navbar/NavBar'

import { request } from "../network/request";

export default {
  name: "AddBlog",
  components: {
    NavBar
  },
  data() {
    return {
      blog: {},
      authors: ["a", "b", "c"],
      submited: false,
      id: this.$route.params.id
    }
  },
  methods: {
    putdata() {
      request({
        url:'/posts/' + this.id + '.json',
        method:'put',
        data: {
          title: this.blog.title,
          content: this.blog.content,
          categories: this.blog.categories,
          author: this.blog.author,
          id: this.blog.id
          // blog: this.blog
        }
      }).then(data => {
        console.log(data);
        this.submited = true
      })
    },
    fetchData() {
      // console.log(this.id);
      request({
        url: '/posts/' + this.id + '.json',
        method: 'get'
      }).then(response => {
        console.log(response);
        this.blog = response.data
      })
    }
  },
  created() {
    this.fetchData()
  }
}
</script>
<style scoped>
  #add-blog * {
    box-sizing: border-box;
  }
  #add-blog {
    margin: 72px auto;
    max-width: 1200px;
    /* padding: 20px; */
  }
  label {
    display: block;
    margin: 20px 0 10px;
    font-size: 18px;
  }
  input[type="text"], textarea, select{
    margin: 0 auto;
    display: block;
    width: 60%;
    padding: 8px
  }
  textarea {
    height: 120px;
  }
  h3 {
    text-align: center;
    font-size: 28px;
  }
  form label {
    margin-left: 20%;
  }
  #checkboxes {
    padding-left: 27%;
    margin: 10px;
  }
  #checkboxes label {
    display: inline-block;
    margin: 8px auto;
  }
  #checkboxes input {
    display: inline-block;
    margin-right: 10px;
  }
  button {
    display: block;
    margin: 20px auto;
    background-color: rgb(239, 207, 253);
    color: #fff;
    border: 0;
    padding: 14px;
    border-radius: 14px;
    font-size: 18px;
    cursor: pointer;
  }

  
  #preview {
    padding: 20px 20px;
    border: 1px dotted #ccc;
    margin: 30px auto;
    width: 80%;
  }
  #preview p {
    font-size: 18px;
  }

  .pngImg {
    position: fixed;
    bottom: 20px;
    right: 0;
  }
  .pngImg img {
    width: 130px;
    height: 186px;
  }
</style>


